import React, { Component } from 'react';
import { debounce } from 'lodash-es';

class FullPage extends Component {

  containerRef = React.createRef();
  pageHeight = window.innerHeight;

  resetHeight = () => {
    this.containerRef.current.style.height = window.innerHeight + 'px';
  }

  debounceResetHeight = debounce(this.resetHeight, 200, {
    leading: true,
  });

  componentDidMount() {
    window.addEventListener('resize', this.debounceResetHeight)
    this.resetHeight();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.debounceResetHeight);
  }

  render() {
    return (
      <div ref={this.containerRef} className="full-page">
        {this.props.children}
      </div>
    );
  }

}

export default FullPage;
